#particles-js {
  //background: $main;
  @include theme() {
    background: t("bg");
  }
  display: flex;
  vertical-align: bottom;
  width: 100%;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  .particles-js-canvas-el {
    width: 100% !important;
    height: 100vh !important;
  }
}

.header {
  z-index: 1;
  text-align: center;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0 auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  .site-title {
    font-size: 50px;
    display: block;
    line-height: 1;
    //color: #FFF;
    @include theme() {
      color: t("text");
    }
    @media only screen and (min-width: $cut) {
      margin: 0;
      font-size: 70px;
    }
  }
  .site-description {
    font-size: 20px;
    display: block;
    line-height: 1;
    color: #fff;
    margin-top: 10px;
    @media only screen and (min-width: $cut) {
      font-size: 40px;
    }
  }
}

.header-links {
  margin: 10px;
  .link {
    //color: #FFF;
    @include theme() {
      color: t("text");
    }
    text-decoration: none;
    font-size: 15px;
    margin: 10px;
    @media only screen and (min-width: $cut) {
      font-size: 20px;
    }
  }
}

.header-line {
  @include theme() {
    border-top: 1px solid t("text");
  }
  width: 50%;
  margin: auto;
  margin-top: 10px;
}

.header-icons {
  display: flex;
  justify-content: center;
  text-align: center;
  .icon {
    text-align: center;
    //color: #FFF;
    @include theme() {
      color: t("text");
    }
    width: 20px;
    height: 20px;
    font-size: 20px;
    padding: 10px;
    margin: 5px;
    border-radius: 50%;
    //border: 2px solid #FFF;
    @include theme() {
      border: 2px solid t("text");
    }
    transition: all 0.7s;
    &:hover {
      //color: $main;
      //background: #FFF;
      @include theme() {
        background: t("text");
        color: t("bg");
      }
    }
    &:active {
      //color: $main;
      //background: #FFF;
      @include theme() {
        background: t("text");
        color: t("bg");
      }
    }
    @media only screen and (min-width: $cut) {
      width: 35px;
      height: 35px;
      font-size: 35px;
    }
  }
}

.down {
  //color: #FFF;
  @include theme() {
    color: t("text");
  }
  position: absolute;
  bottom: 25px;
  width: 100%;
  margin: 0 auto;
  display: block;
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  .icon {
    position: absolute;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    //fill: #fff;
    @include theme() {
      fill: t("text");
    }
    -webkit-animation: pulse 1.3s infinite;
    animation: pulse 1.3s infinite;
    &:hover {
      color: $sec;
    }
    &:active {
      color: $sec;
    }
  }
}
